* {
    box-sizing: border-box;
  }

.App {
    margin: auto;
}
  
  body {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    color: #444;
    line-height: 1.5;
    text-align: center;
    min-width: 320px;
    padding-bottom: 10px;
    padding-top: 10px;
  }
  
  header {
    text-align: center;
  }
  
  section {
    min-height: 200px;
    padding: 30px 0px;
    background-color: #bbb;
  }
  
  section:nth-child(even) {
    background-color: #ddd;
  }
  
  p, h2 {
    margin: 23px 0;
  }
  
  h2 {
    font-weight: 700;
    font-size: 20px;
  }
  
  form {
    text-align: center;
    margin: 0 auto;
    padding: 0 0 40px;
    max-width: 650px;
  }
  
  label p {
    margin: 6px 0 23px;
  }
  
  p + p {
    margin-top: 15px;
  }

  p {
    margin: 0 auto;
    max-width: 600px;
  }
  
  label {
    display: block;
  }
  
  footer {
    background-color: #aaa;
    text-align: center;
    left: 0px;
    bottom: 0;
    height: 30px;
    width: 100%;
    position: fixed;
  }

  nav {
    display: flex;
    justify-content: space-evenly;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    padding: 1% 0;
    background-color: #aaa;
  }

  button {
    margin-bottom: 50px;
}

.map {
  margin: auto;
  width: 60%;
}

.hike-card h3 {
  font-size: 20px;
  margin-left: 10px;
}

.hike-card p {
  font-size: 14px;
  line-height: 20px;
}

.hike-card-subtext {
  font-size: 10px;
  color: #444;
}

.rating {
  margin-top: 10px;
  margin-bottom: 20px;
}

.hike-card-text {
  line-height: 10px;
}

.hike-card {
  display: flex;
  background-color: white;
  border: 1px solid black;
  border-radius: 5px;
  height: auto;
  width: auto;
  margin-bottom: 20px;
}

.hike-card-img {
  object-fit: cover;
  width: 50%;
  height: 100%;
  background-color: green;
}